
.fa-icon {
  vertical-align: middle;
  margin-right: 10px;
}
.mint-cell:last-child {
  background: none;
}
.sidebar-container {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 75%;
  height: 100%;
  box-shadow: 4px 0 18px 6px rgba(0, 0, 0, .12);
  background-color: #fff;
  z-index: 999;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
}
.sidebar-container .user {
  color: #666;
  text-align: center;
  margin: 30px 0 40px;
}



