
.other-service {
    position: absolute;
    background: #fff;
    /*top: 86px;*/
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px 5%;
    text-align: center;
    -webkit-transform: translateY(0) translateZ(0);
    transform: translateY(0) translateZ(0);
    z-index: 9;
}
.other-service .single-icon {
    position: relative;
    display: inline-block;
    width: 25%;
    text-align: center;
    margin: 0 7px 20px;
}
.other-service .single-icon .border-round {
    position: relative;
    display: inline-block;
    width: 32px;
    height: 32px;
}
.other-service .single-icon .border-round img {
    display: inline-block;
    width: 32px;
    height: 32px;
    margin-bottom: 10px;
    border-radius: 16px;
}
.other-service .single-icon p {
    font-size: 12px;
    color: #333;
    height: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
}



