
.call-wrapper {
  width: 100%;
  padding-top: 150px;
  -webkit-animation-duration: .5s;
          animation-duration: .5s;
}
.call-tips {
  height: 60px;
  /*line-height: 60px;*/
}
.call-tips .title {
  font-size: 20px;
}
.loader {
  position: relative;
}
.loader-tip {
  width: 100%;
  position: absolute;
  top: 88px;
  color: #FC9153;
}
.loader-tip>p {
  color: #6C6C6C;
}
.loader-tip .notice-car {
  font-size: 30px;
}
.circle-wapper {
  width: 110px;
  height: 40px;
  position: absolute;
  top: 98px;
  left: 50%;
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-animation: circleRoate infinite linear 30s;
          animation: circleRoate infinite linear 30s;
  z-index: 99;
}
.circle-wapper .circle-demo span {
  font-size: 12px;
  color: #fff;
  line-height: 40px;
}
@-webkit-keyframes circleRoate {
to {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg)
}
}
@keyframes circleRoate {
to {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg)
}
}
.circle-one {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  stroke-dasharray: 566px;
  stroke-dashoffset: 566px;
  -webkit-animation: progressAnimation 30s linear infinite;
          animation: progressAnimation 30s linear infinite;
}
@-webkit-keyframes progressAnimation {
to {
    stroke-dashoffset: 0px;
}
}
@keyframes progressAnimation {
to {
    stroke-dashoffset: 0px;
}
}
.circle-demo {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #FC9153;
  position: absolute;
  right: 0;
}
.call-wrapper .bottom {
  width: 100%;
  /*padding: 0 20px;*/
  position: absolute;
  bottom: 40px;
}
.call-wrapper .bottom>div{
  width: 80%;
  margin-left: 10%;
  box-shadow: 1px 1px 10px #ccc;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.call-wrapper .bottom button {
  background: #fff;
  border: none;
  padding: 10px 10px;
}




